
import XHeader from 'vux/src/components/x-header/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'


export default {
data () {
return {
switchIndex: 0,
draft: {
list: '',
stockEndTime: '',
stockProductClientSimpleDTO: '',
type: 0
},
year: new Date().getFullYear().toString(),
stockList: [],
stock: {
list: '',
stockEndTime: '',
stockProductClientSimpleDTO: ''
},
ifShowYear: false,
stockIcon: require('../../assets/images/doc_ic.png')
}
},
watch: {
year (val) {
this._getStock(this.switchIndex)
}
},
methods: {
goBack () {
this.$util.getMachine(4)
},
changeYear () {
this.ifShowYear = true
},
changeDate () {
},
formatThousand (num, leng) {
let len = 0
if (leng) {
len = leng
}
let DIGIT_PATTERN = /(^|\s)\d+(?=\.?\d*($|\s))/g
let MILI_PATTERN = /(?=(?!\b)(\d{3})+\.?\b)/g
if (num) {
return num.toFixed(len).toString().replace(DIGIT_PATTERN, (m) => m.replace(MILI_PATTERN, ','))
} else {
return (0).toFixed(len)
}
},
switchTabItem (index) {
this.switchIndex = index
this._getStock(index)
},
toAdd () {
this.$router.push({
path: '/addStock'
})
},
toStockDetail (id, canSave) {

if (canSave) {
this.$router.push({
path: '/addStock',
query: {
stockId: id
}
})
} else {
this.$router.push({
path: '/stockView',
query: {
stockId: id
}
})
}
},
_getStock (index) {
this.switchIndex = index
if (index === 0) {
this.$tupHttp(this, 'front/stockProduct/getDraft', 'POST').then(res => {
this.draft = res.data.model
if (this.draft.list) {
this.stockList = this.draft.list
}
})
} else if (index === 1) {
this.stockList = []
this.$tupHttp(this, 'front/stockProduct/getSubmit/' + this.year).then(res => {
this.stock = res.data.model
this.stockList = res.data.model.list ? res.data.model.list : []
})
}
}
},
created () {
this._getStock(this.switchIndex)
},
components: {
XHeader,
Tab,
TabItem,
Datetime
}
}



